.project {
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;;
  transition: transform 0.2s linear;
}

.project:hover {
  transform: translateY(8px);
}

.project__description {
  margin-top: 1em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
  transform: translate(-15px ,0);
}

.project__stack-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: #444;
  list-style: none;
}

.project .link--icon {
  margin-left: 0.5em;
}
